

body {
    width: 100vw;
    height: 100vh;
    background: white;
    overflow: hidden;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Karla', sans-serif;
  }
  
  nav {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
  }
  
  ul,
  li {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  ul {
    position: relative;
    display: flex;
    
    flex-wrap: wrap;
  }
  
  li {
    border-radius: 0px;
    margin-top: 75px;
    margin-bottom: 10px;
    margin-right: 2px;
    margin-left: 2px;
    width: 14px;
    height: 42px;
  }
  
  .sty {
      font-size:40px;
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
    background: #fff;
  }
  
  .refresh {
    padding: 10px;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    width: 20px;
    height: 20px;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  